.select-group {
  @apply tw-relative;
  .icon {
    @apply tw-absolute tw-right-2 tw-top-1/2 -tw-translate-y-1/2 tw-w-3 tw-pointer-events-none;
  }
}
.select-field {
  @apply tw-block tw-w-full tw-cursor-pointer tw-bg-gray-50 tw-border tw-border-gray-300 tw-text-gray-900 tw-text-sm tw-rounded tw-appearance-none;
  @apply hover:tw-opacity-90;  
  @apply focus:tw-ring-blue-500 focus:tw-border-blue-500  tw-p-2.5;
  @apply focus-visible:tw-outline-offset-0;
}
